.main-nav__logo {
  fill: #FDFDFD;
  margin-right: 78px;
}
.main-nav {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  color: #FDFDFD;
  padding-top: 30px;
  margin-bottom: 160px;
}
.main-nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__item--selected {
  margin-right: 42px;
}
.main-nav__item--selected {
  position: relative;
}
.main-nav__arrow {
  fill: none;
  stroke: #FDFDFD;
  position: absolute;
  margin-left: 5px;
  cursor: pointer;
}
.main-nav__item:nth-child(5) {
  margin-right: 15px;
}
.main-nav__item--search {
  margin-right: 0;
}
.main-nav__button--check {
  margin-left: 144px;
  margin-right: 15px;
}
.main-nav__button {
  border-radius: 25px;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  padding: 14px 20px;
  cursor: pointer;
}
.main-nav__button--check {
  border: 2px solid #DF0050;
  background-color: inherit;
  color: #FFFFFF;
  min-width: 279px;
}
.main-nav__button--buy {
  background-color: #FFFFFF;
  color: #49301D;
  width: 99px;
  border: none;
}
.main-header {
  background-image: url("/img/main-header.png");
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-header__title {
  font-weight: 500;
  font-size: 80px;
  line-height: 110%;
  color: #FFFFFF;
  max-width: 652px;
  margin-bottom: 40px;
}
.main-header__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
  padding-bottom: 84px;
  position: relative;
}
.main-header__arrow {
  transform: rotate(-90deg);
  fill: none;
  stroke: #FDFDFD;
  position: absolute;
  margin-left: 15px;
}
* {
  margin: 0;
  padding: 0;
  font-family: "Graphik LC Web", "Arial", sans-serif;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  margin: 0 50px;
}
.dropdown-content {
  display: none;
  position: absolute;
  margin-top: 91px;
  left: -230px;
  z-index: 1;
  background: linear-gradient(to right, rgba(48, 57, 75), rgba(17, 23, 33));
}
.dropdown-content__list {
  list-style-type: none;
  display: grid;
  grid-template-columns: 450px 450px 450px;
}
.dropdown-content__item {
  margin-bottom: 35px;
  padding: 15px;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
}
.dropdown-content__show--first {
  display: block;
}
.dropdown-content__show--second {
  display: block;
  left: -340px;
}
.dropdown-content__show--third {
  display: block;
  left: -460px;
}
.links {
  margin-top: 50px;
}
.links__list {
  display: flex;
  list-style-type: none;
  max-height: 150px;
  flex-wrap: wrap;
  justify-content: space-between;
}
.links__item {
  border-top: 1px solid #DADCE7;
  padding: 25px 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  min-width: 416px;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  color: #252627;
}
.links__arrow {
  transform: rotate(-90deg);
  stroke: #1E5AAF;
  fill: none;
  margin-right: 10px;
}
.about {
  margin-top: 145px;
  margin-bottom: 120px;
}
.about__title {
  font-weight: 600;
  font-size: 40px;
  line-height: 130%;
  color: #1E5AAF;
  margin-bottom: 40px;
}
.about__description {
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: #797A84;
  max-width: 646px;
  margin-bottom: 30px;
}
.about__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}
.about__item {
  width: 300px;
  height: 413px;
  background: linear-gradient(0deg, rgba(9, 46, 98, 0.3) 0%, rgba(65, 124, 209, 0.3) 100%), #1E5AAF;
  box-shadow: 0px 0px 10px rgba(6, 31, 67, 0.3);
  border-radius: 5px;
  position: relative;
  padding: 35px;
}
.about__header {
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
  color: #FFFFFF;
  margin-bottom: 15px;
}
.about__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: rgba(253, 253, 253, 0.5);
}
.about__arrow {
  fill: none;
  stroke: #FFFFFF;
  transform: rotate(-90deg);
  position: absolute;
  bottom: 40px;
  right: 30px;
}
.notes__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}
.notes__button {
  background-color: inherit;
  border: none;
}
.notes__button--left {
  transform: rotate(90deg);
  margin-right: 50px;
}
.notes__button--right {
  transform: rotate(-90deg);
}
.notes__arrow {
  fill: none;
  stroke: #2864B9;
}
.notes__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #1E5AAF;
}
.notes__list {
  list-style-type: none;
  display: flex;
  margin-bottom: 133px;
}
.notes__item {
  display: flex;
  flex-direction: row;
  background: linear-gradient(0deg, rgba(9, 46, 98, 0.3) 0%, rgba(65, 124, 209, 0.3) 100%), #1E5AAF;
  border-radius: 5px 5px 5px 5px;
}
.notes__text-wrapper {
  width: 460px;
  height: 550px;
  display: flex;
  padding: 45px;
  flex-direction: column;
}
.notes__description {
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: #FDFDFD;
  margin-bottom: 20px;
}
.notes__date {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #8EA9D0;
  margin-bottom: 40px;
}
.notes__link {
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #FDFDFD;
  display: flex;
}
.notes__arrow-list {
  fill: none;
  stroke: #FFFFFF;
  transform: rotate(-90deg);
  margin-left: 10px;
}
.notes__item--second {
  overflow: hidden;
}
.opinions {
  margin-bottom: 120px;
}
.opinions__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}
.opinions__button {
  background-color: inherit;
  border: none;
}
.opinions__button--left {
  transform: rotate(90deg);
  margin-right: 50px;
}
.opinions__button--right {
  transform: rotate(-90deg);
}
.opinions__arrow {
  fill: none;
  stroke: #2864B9;
}
.opinions__text {
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  color: #1E5AAF;
}
.opinions__title {
  font-weight: 600;
  font-size: 40px;
  line-height: 130%;
  color: #252627;
  width: 1013px;
  margin-bottom: 40px;
}
.opinions__list {
  list-style-type: none;
  display: flex;
  flex-direction: row;
}
.opinions__item {
  padding: 61px 97px 64px 110px;
  box-shadow: 0px 0px 10px rgba(6, 31, 67, 0.3);
  border-radius: 5px;
  min-width: 879px;
  min-height: 575px;
}
.opinions__header {
  font-weight: 400;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #FFFFFF;
  position: relative;
  margin-bottom: 20px;
}
.opinions__image {
  position: absolute;
  right: 0;
  top: 5px;
}
.opinions__merit {
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: #557EB8;
  max-width: 446px;
  margin-bottom: 76px;
}
.opinions__description {
  font-weight: 400;
  font-size: 30px;
  line-height: 140%;
  color: #9FB7DB;
  max-width: 672px;
  margin-bottom: 65px;
}
.opinions__link {
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
}
.opinions__arrow-link {
  fill: none;
  stroke: #FFFFFF;
  transform: rotate(-90deg);
}
.opinions__item--first {
  background: linear-gradient(0deg, rgba(9, 46, 98, 0.3) 0%, rgba(65, 124, 209, 0.3) 100%), #1E5AAF;
  margin-right: 44px;
}
.opinions__item--second {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(0deg, rgba(9, 46, 98, 0.3) 0%, rgba(65, 124, 209, 0.3) 100%), #1E5AAF;
  overflow: hidden;
}
